<template>
  <div id="app">
    <Header/>
    <div class="container pt-fixed pb-2">
      <router-view />
    </div>
    <b-button @click="show = true" v-if="productInfo.length" variant="info" class="dishes-info d-flex align-items-center justify-content-center">
      {{productInfo.length}}
    </b-button>
    <b-modal v-model="show" hide-header id="productInfo" title="BootstrapVue">
      <div class="product-dishes-list" v-if="productInfo.length">
        <div v-for="(item, index) in productInfo" :key="index" class="product-dishes d-flex w-100 align-items-center">
          <p class="mr-2">{{item.title}}</p>
          <b-button @click="removeItemsDishes(index)" size="sm" variant="danger" class="d-flex ml-auto">
            <b-icon icon="x-circle" aria-hidden="true"></b-icon>
          </b-button>
        </div>
      </div>
      <template #modal-footer>
        <div class="w-100 d-flex">
          <b-button
              variant="primary"
              size="sm"
              class="mr-2 w-100"
              @click="show=false"
          >
            Закрыть
          </b-button>
          <b-button
              variant="success"
              size="sm"
              class="w-100"
              @click="[($router.push({name: 'DishesNew'})), (show=false)]"
          >
            Ок
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>
<script>
import Header from '@/components/Header.vue'
// import Footer from "./components/Footer.vue";

export default{
  name: 'App',
  components: {
    Header
  },
  data() {
    return {
      isOpen: false,
      showBasket: false,
      basket: [],
      show: false
    }
  },
  computed: {
    productInfo() {
      return this.$store.state.productInfo;
    }
  },
  // watch: {
  //   productIds: function (val) {
  //     if(val.length) {
  //       this.showBasket = true;
  //       this.setDishesBasket();
  //     }
  //   }
  // },
  methods: {
    async setDishesBasket() {
      try {
        await this.$pouch.allDocs({
          // selector: { is_home: 1 },
          // fields: ['_id', 'title'],
          keys: this.productInfo
        }).then((res) => {
          this.basket = res.rows
        });
      } catch (error) {
        console.error(error)
      }
    },

    removeItemsDishes(index) {
      this.$store.dispatch('setProductRemove', index);
      setTimeout(() => {
        this.show = false;
      }, 500)

      // this.products.splice(index, 1);
    }
  }
}
</script>
