import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        productInfo: [],
        productIds: [],
        option: {
            bx_param: localStorage.getItem('bx_param') || 0.4,
            bj_param: localStorage.getItem('bj_param') || 0.3
        }
    },
    mutations: {
        setIds(state, code) {
            const result = [];
            const duplicatesIndices = [];
            state.productInfo.push(code);
            // Перебираем каждый элемент в исходном массиве
            state.productInfo.forEach((current, index) => {
                if (duplicatesIndices.includes(index)) return;
                result.push(current);
                for (let comparisonIndex = index + 1; comparisonIndex < state.productInfo.length; comparisonIndex++) {
                    const comparison = state.productInfo[comparisonIndex];
                    const currentKeys = Object.keys(current);
                    const comparisonKeys = Object.keys(comparison);
                    if (currentKeys.length !== comparisonKeys.length) continue;
                    const currentKeysString = currentKeys.sort().join("").toLowerCase();
                    const comparisonKeysString = comparisonKeys.sort().join("").toLowerCase();
                    if (currentKeysString !== comparisonKeysString) continue;
                    let valuesEqual = true;
                    for (let i = 0; i < currentKeys.length; i++) {
                        const key = currentKeys[i];
                        if ( current[key] !== comparison[key] ) {
                            valuesEqual = false;
                            break;
                        }
                    }
                    if (valuesEqual) duplicatesIndices.push(comparisonIndex);
                }
            });
            result.forEach((current) => {
                if (!current.dishes_gram) {
                    current.dishes_gram = 100
                }
            })
            state.productInfo = result;
            // state.productIds = Array.from(new Set(state.productIds));
        },
        setRemove(state, index) {
            state.productInfo.splice(index, 1);
        },
        setRemoveAll(state) {
            state.productInfo = [];
        },
        setOptionBx(state, param) {
            localStorage.setItem('bx_param', param);
            state.option.bx_param = param;
        },
        setOptionBj(state, param) {
            localStorage.setItem('bj_param', param);
            state.option.bj_param = param;
        }
    },
    actions: {
        /**
         * Example usage in Vue components: store.dispatch('setAuthToken', token);
         * @param {Store} context Signature: context.state, context.commit, etc..
         * @param {Object} code
         */
        setProductDishes(context, code) {
            context.commit('setIds', code);
        },
        setProductRemove(context, code) {
            context.commit('setRemove', code);
        },
        setProductRemoveAll(context) {
            context.commit('setRemoveAll');
        },
    }
});

export default store;
